import Grid from '@mui/material/Grid';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Box from '@mui/material/Box';
import {roundDigit, roundWithError, crab_flux} from './Utils.js'


function eventInfo(run_db){
    return(
      <div>
        (N<sub>on</sub>, N<sub>off</sub>, &alpha;) = ({run_db.N_on}, {run_db.N_off}, {roundDigit(run_db.alpha,3)})
      </div>
      )
  }
  
export function runSummary(run_db){

    return(
      <Box sx={{ flexGrow: 1 }} style={{height: "350px", width:"100%"}}>
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <ListItem style={{paddingBottom: "0px", marginBottom:"0px"}}>
            <ListItemText primary="Target" secondary={run_db.event}/>
          </ListItem>
        </Grid>
        <Grid item xs={6}>
          <ListItem style={{paddingBottom: "0px", marginBottom:"0px"}}>
            <ListItemText primary="Location [degrees]" secondary={"(RA, DEC, Wobble) = ("+roundDigit(run_db.ra, 1)+", "+roundDigit(run_db.dec, 1)+", "+run_db.wobble+")"}/>
          </ListItem>
        </Grid>
        <Grid item xs={6} style={{paddingTop: "0px", marginTop:"0px"}}>
          <ListItem style={{paddingBottom: "0px", marginBottom:"0px"}}>
            <ListItemText primary="Time [UTC]" secondary={run_db.tmin+" - "+run_db.tmax}/>
          </ListItem>
        </Grid>
        <Grid item xs={6} style={{paddingTop: "0px", marginTop:"0px"}}>
          <ListItem style={{paddingBottom: "0px", marginBottom:"0px"}}>
            <ListItemText primary="Exposure / Livetime [min]" secondary={roundDigit(run_db.exposure/60,1)+" / "+roundDigit(run_db.livetime/60,1)}/>
          </ListItem>
        </Grid>
        <Grid item xs={6} style={{paddingTop: "0px", marginTop:"0px"}}>
          <ListItem style={{paddingBottom: "0px", marginBottom:"0px"}}>
            <ListItemText primary="Events" secondary={eventInfo(run_db)}/>
          </ListItem>
        </Grid>
        <Grid item xs={6} style={{paddingTop: "0px", marginTop:"0px"}}>
          <ListItem style={{paddingBottom: "0px", marginBottom:"0px"}}>
            <ListItemText primary="Significance" secondary={roundDigit(run_db.sigma, 3)}/>
          </ListItem>
        </Grid>
        <Grid item xs={6} style={{paddingTop: "0px", marginTop:"0px"}}>
          <ListItem style={{paddingBottom: "0px", marginBottom:"0px"}}>
            <ListItemText primary="Flags" secondary={"(Weather, Run, Fit) = ("+run_db.weather_flag+", "+run_db.run_flag+", "+run_db.pl_fit_flag+")"}/>
          </ListItem>
        </Grid>
        {(run_db.pl_fit_flag === "A" ||run_db.pl_fit_flag === "B" )&&[
        <Grid item xs={6} style={{paddingTop: "0px", marginTop:"0px"}}>
          <ListItem style={{paddingBottom: "0px", marginBottom:"0px"}}>
            <ListItemText primary="Photon index" secondary={roundDigit(run_db.pl_index, 3)+"+/-"+roundDigit(run_db.pl_index_err, 3)}/>
          </ListItem>
        </Grid>,
        <Grid item xs={6} style={{paddingTop: "0px", marginTop:"0px"}}>
          <ListItem style={{paddingBottom: "0px", marginBottom:"0px"}}>
            <ListItemText primary={<div>Differential flux at 1 TeV [1/TeV/cm<sup>2</sup>/s]</div>} secondary={roundWithError(run_db.pl_amplitude, run_db.pl_amplitude_err, 3)}/>
          </ListItem>
        </Grid>,
        <Grid item xs={6} style={{paddingTop: "0px", marginTop:"0px"}}>
          <ListItem style={{paddingBottom: "0px", marginBottom:"0px"}}>
            <ListItemText primary={<div>Integrated flux (0.1-30 TeV) [TeV/cm<sup>2</sup>/s]</div>} secondary={roundWithError(run_db.pl_e2dnde, run_db.pl_e2dnde_err, 3)+"("+roundDigit(run_db.pl_e2dnde/crab_flux, 4)+" % of Crab)"}/>
          </ListItem>
        </Grid>,]}
      </Grid>
      </Box>

      
      )
  }