import React, { Component } from 'react';


class Plot extends Component {
    
  constructor(props) {
        super(props);
        this.state = {
          iFrameHeight: '350px',
          srcId: this.props.srcId,
          runId: this.props.runId,
          pltsrc: this.props.pltsrc,
        }        
  }
  
  componentDidMount() {
    if (this.props.srcId === -1){
      this.setState({srcId: -1, runId: -1, pltsrc: "/main_plot/"})
    } else {
      this.setState({srcId: this.props.srcId, runId: this.props.runId, pltsrc: this.genPltSrc(this.props.pltsrc,this.props.srcId,this.props.runId)})
    }
  }

  componentDidUpdate(prevProbs) {
    if ((prevProbs.srcId !== this.props.srcId) || (prevProbs.runId !== this.props.runId)) {
      this.setState({srcId: this.props.srcId, runId: this.props.runId, pltsrc: this.genPltSrc(this.props.pltsrc,this.props.srcId,this.props.runId)})
    }
  }

  genPltSrc(pltsrc, runId, srcId){
    return (pltsrc+"?src="+runId+"&run="+srcId)
  }


  render() {
    const pltsrc = this.state.pltsrc
    console.log(pltsrc)
    return (
      <iframe src={pltsrc}
        style={{width:'100%', height:this.state.iFrameHeight, overflow:'visible'}}
        ref="iframe" 
        width="100%" 
        height={this.state.iFrameHeight} 
        scrolling="no" 
        frameBorder="0"
       />
      )
  }
}
export default Plot;