export function roundDigit(val, digit){
    return Math.round(val*Math.pow(10,digit))/Math.pow(10,digit)
}

export function roundWithError(val, val_err, digit){
    const exp = Math.round(Math.log10(val))
    val = Math.round(val/10**exp*Math.pow(10,digit))/Math.pow(10,digit)
    val_err = Math.round(val_err/10**exp*Math.pow(10,digit))/Math.pow(10,digit)
    return "("+val+"+/-"+val_err+")e"+exp

}

export const crab_flux = 4.722281080894965e-10