import React, { Component } from 'react';
import TABLE from './Table'
import './App.css';
import Plot from './Plot'

import veritasIcon from './image/veritas.png'

import {
  AtlassianNavigation,
  PrimaryButton,
  PrimaryDropdownButton,
  CustomProductHome,
  Search,
} from '@atlaskit/atlassian-navigation';

import DropdownMenu, { DropdownItem, DropdownItemGroup } from '@atlaskit/dropdown-menu';

import Tabs, { Tab, TabList, TabPanel } from '@atlaskit/tabs';

import {runSummary} from './Runs'

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      runId: -1,
      srcId: -1,
      targetName: undefined,
      search: "",
      run_db: [],
    };
    this.changeIdx = this.changeIdx.bind(this)
    
    this.defaultSearch = this.defaultSearch.bind(this)

    this.getRunInfo = this.getRunInfo.bind(this)
  }

  changeIdx(srcId, name, runId){
    this.setState({srcId: srcId, targetName:name, runId:runId})
  }

  defaultSearch(){    
    return (
      <Search
        onClick={(e)=>{this.setState({search: e.target.value})}}
        placeholder="Search..."
        tooltip="Search"
        label="Search"
        value={this.state.search}
      />
    );
  }

  getRunInfo = () => {
    const runId = this.state.runId
    fetch('run/'+runId)
    .then(response => response.json())
    .then(response => this.setState({run_db: response.data}))
    .catch(err => console.error(err))
  }

  getSummary(){
    this.getRunInfo()
    return runSummary(this.state.run_db)
  }
  

  render(){
    const {srcId, runId, targetName} = this.state
    
    return (
    <div>
      <AtlassianNavigation
        label="site"
        renderProductHome={() => (
          <CustomProductHome href="#"
          iconUrl={veritasIcon}
          iconAlt="veritas"
          logoUrl={veritasIcon}
          logoAlt="veritas"
          siteTitle="VERITAS Next-Day Analysis"/>)}
        primaryItems={[
          <DropdownMenu trigger={({ triggerRef, ...props }) => (
          <PrimaryDropdownButton {...props} ref={triggerRef}>Database</PrimaryDropdownButton>)}>
            <DropdownItemGroup>
              <DropdownItem>Run</DropdownItem>
              <DropdownItem>Daily</DropdownItem>
              <DropdownItem>Source</DropdownItem>
              <DropdownItem>User</DropdownItem>
            </DropdownItemGroup>
            </DropdownMenu>,
          <PrimaryButton>Request</PrimaryButton>,
          <PrimaryButton>Info</PrimaryButton>,
        ]}
        renderSearch={this.defaultSearch}
      />
      <div style={{marginRight: "30px", marginLeft: "30px"}}>
        {(srcId === -1)?
          <div>
            <h3 style={{marginBottom: "10px", paddingLeft: "10px", paddingBottom:"0px", color:"rgba(107, 119, 140, 1)"}}> All sky map </h3>
            <Plot srcId={srcId} changeIdx={this.changeIdx}/>
          </div>:
          <div>
            <h3 style={{marginBottom: "10px", paddingLeft: "10px", paddingBottom:"0px", color:"rgba(107, 119, 140, 1)"}}> Run: {runId} ({targetName})</h3>
            <Tabs id="default">
              <TabList>
                <Tab>Summary</Tab>
                <Tab>History</Tab>
                <Tab>Anasum</Tab>
                <Tab>Analysis</Tab>
              </TabList>
              <TabPanel>
                {this.getSummary()}
              </TabPanel>
              <TabPanel>
                <Plot srcId={srcId} runId={runId} pltsrc={"/evt_plot"} changeIdx={this.changeIdx}/>
              </TabPanel>
              <TabPanel>
                <Plot srcId={srcId} runId={runId} pltsrc={"/anasum"} changeIdx={this.changeIdx}/>
              </TabPanel>
              <TabPanel>
                <Plot srcId={srcId} runId={runId} pltsrc={"/fit"} changeIdx={this.changeIdx}/>
              </TabPanel>
            </Tabs>
          </div>}
        <TABLE srcId={srcId} changeIdx={this.changeIdx}/>
      </div>
    </div>
  );
  }
}

export default App;


