import React, { Component } from 'react';
import {Table, Column, AutoSizer, defaultTableRowRenderer, SortDirection, SortIndicator} from 'react-virtualized';
import 'react-virtualized/styles.css';
import styles from './TABLE.module.css'
import Spinner from '@atlaskit/spinner';

class TABLE extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            db: [],
            db_selected: [],
            init: false,
            editDialog: false,
            sortBy: "run_id",
            sortDirection: SortDirection.DESC,
            srcId: this.props.srcId,
            runId: undefined,
            searchText: undefined,
        }

        this._rowClassName = this._rowClassName.bind(this)
        this._searchFor = this._searchFor.bind(this)
        this._sortData = this._sortData.bind(this)
        this._onRowClick = this._onRowClick.bind(this)
        this._headerRenderer = this._headerRenderer.bind(this)
    }

    componentDidMount() {
        this.getDatabase();
    }

    getDatabase = () => {
        fetch('runs')
        .then(response => response.json())
        .then(response => this.setState({db: response.data.reverse(), db_selected: response.data}))
        .catch(err => console.error(err))
    }

    _sortData({ sortBy, sortDirection }) {
        let { db, db_selected } = this.state

        db_selected.sort(this.sortByProperty(sortBy))

        if (sortDirection === SortDirection.DESC){
            db_selected = db_selected.reverse()
        }
        this.setState({ sortBy, sortDirection, db, db_selected});
    }

    sortByProperty(property){  
        return function(a,b){  
            if(a[property] > b[property])  
                return 1;  
            else if(a[property] < b[property])  
                return -1;  
            return 0;  }
    }  

    _rowClassName({index}) {
        if (index < 0) {
            return styles.headerRow;
        } else {
            return index % 2 === 0 ? styles.evenRow : styles.oddRow;
        }
    }

    _searchFor(e) {
        const { db } = this.state
        const searchText = e.target.value

        this.setState({db_selected: db.filter((element) => {
            return (element.event.indexOf(searchText) !== -1)
        })
        })

        this.setState({searchText});
    }

    _headerRenderer({ label, dataKey, sortBy, sortDirection }) {
        return (
            <div>
            { label }
            { sortBy === dataKey && <SortIndicator sortDirection={sortDirection} />}
            </div>
        );
    }

    _onRowClick ({rowData, index}) {
        const { runId } = this.state
        if ( runId === rowData.run_id ) {
            this.setState({srcId: -1, runId: -1})
            this.setState({db_selected: this.state.db})
            this.props.changeIdx(-1, undefined, -1)
        } else {
            this.props.changeIdx(rowData.src_id, rowData.event, rowData.run_id)
            this.setState({srcId: rowData.src_id, runId:rowData.run_id}) 
            this.setState({db_selected: this.state.db.filter((element) => {
            return (
            (element.src_id === rowData.src_id
            ))})})
            
        }
    }


    rowRenderer = prevProps => {
        const { style, key, rowData, className } = prevProps
        const { runId } = this.state
        
        if (rowData.run_id === runId) {
            return (
            <div
            style={{ ...style, display: "flex", flexDirection: "column" }}
            key={key}
            className={className}
            >
            {defaultTableRowRenderer({
            ...prevProps,
            style: {width:style.width, height:style.height, fontWeight:"bold"}
            })}
            </div>
        );
        } else{
            return defaultTableRowRenderer({...prevProps, style:{...style}});    
        }
    };

    _cellFlux = ({ rowData }) => {
        var val, digit, val_err

        if (rowData.pl_fit_flag === "B") {
            val = rowData.pl_e2dnde_ul
            digit = Math.round(Math.log10(val))
            val = Math.round(val/10**digit*1000)/1000
            return "< "+val+"e"+digit
        } else if (rowData.pl_fit_flag === "A"){
            val = rowData.pl_e2dnde
            digit = Math.round(Math.log10(val))
            val = Math.round(val/10**digit*100)/100
            val_err = rowData.pl_e2dnde_err
            val_err = Math.round(val_err/10**digit*100)/100
            return "("+val+"+/-"+val_err+")e"+digit
        } else {
            return ""
        }
        
    }

    render() {

        const { db, db_selected, sortBy, sortDirection, scrollToIndex } = this.state

        const _getDatum = index => db_selected[index % db_selected.length];
        
        const rowGetter = ({ index }) => _getDatum(index);

        if (db.length === 0){
            return <Spinner size={"medium"} style={{width:"100%"}}/>
        } else {
            return (
            <div>
                <AutoSizer disableHeight>
                {({width}) => (
                <Table
                width={width}
                height={600}
                headerHeight={30}
                rowHeight={30}
                rowCount={db_selected.length}
                rowGetter={rowGetter}
                sort={this._sortData}
                sortBy={sortBy}
                onRowClick={this._onRowClick}
                rowRenderer={this.rowRenderer}
                sortDirection={sortDirection}
                rowClassName={this._rowClassName}
                scrollToIndex={scrollToIndex}
                >

                <Column
                    disableSort={false}
                    headerRenderer={this._headerRender}
                    label="#"
                    dataKey="run_id"
                    width={70} 
                />

                <Column 
                    disableSort={false}
                    label="Time" 
                    dataKey="tmin" 
                    cellRenderer={({ rowData }) => {return rowData.tmin.split("T")[0]}}
                    width={120}
                />

                <Column 
                    disableSort={false}
                    label="Target" 
                    dataKey="event" 
                    width={200}
                />
                <Column 
                    disableSort={false}
                    label="Weather" 
                    dataKey="weather_flag" 
                    width={90}
                />

                <Column 
                    disableSort={false}
                    label="Run"
                    dataKey="run_flag" 
                    width={100}
                />

                <Column 
                    disableSort={false}
                    label="Fit" 
                    dataKey="pl_fit_flag" 
                    width={70}
                />

                <Column 
                    disableSort={false}
                    label="Sigma"
                    dataKey="sigma" 
                    cellRenderer={({ rowData }) => {return Math.round(rowData.sigma*1000)/1000}}
                    width={90}
                />
                <Column 
                    disableSort={false}
                    label="Expo" 
                    dataKey="exposure" 
                    cellRenderer={({ rowData }) => {return Math.round(rowData.exposure/60*10)/10}}
                    width={90}
                />
                <Column 
                    disableSort={false}
                    label="E2dNdE" 
                    dataKey="pl_e2dnde" 
                    cellRenderer={this._cellFlux}
                    width={150}
                />

                </Table>
                )}
                </AutoSizer>
            </div>
        )}
    }
}

export default TABLE;
